import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { HttpClient, HttpEventType, HttpResponse, HttpHeaders } from '@angular/common/http';

@Component({
	selector: 'sml-singlefileupload',
	template: `
	<div class="w-100">
	<div class="form-row align-items-center">
		<div class="col-12 mb-0">
			<label class="col-md-12 dropdownarea" for="{{idInputFile}}">
				<div class="d-flex w-100 flex-column">
					<input type="file" id="{{idInputFile}}" (change)="upload($event.target.files)" class="drag"
						[ngStyle]="{'height.px': height,'width.px': width}" />
					<div class="d-flex align-content-start align-items-start w-100 flex-column">
						<div class="d-flex align-content-start align-items-start">
							<span class="uploadicon"><img src="./assets/upload.png" /></span>
							<div class="d-flex flex-column position-relative w79">
								<div class="fz14 top5" [ngClass]="{'getData':isFiles}">Choose a file or drag it here. 
									<span *ngIf="percentDone"> {{percentDone}}%</span></div>
								<div class="progressContainer">
									<div class="procesbar"></div> 
									<div class="w-100 z9" *ngIf="percentDone">
										<div class="progressbar" [ngStyle]="{'width.%':percentDone}"></div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</label>

			<div class="listdata mt-5" *ngIf="UploadedFileli">
				<ul class="w-100 fileuploadlist">
					<li *ngFor="let data of datas; let i = index"
						class="w-100 d-flex justify-content-between align-content-around">
						<div class="w-100 mflex" *ngIf="contentIndex != i">
							<a href="{{data.FileUrl}}" target="_blank">{{data.Description}}</a>
							<div class="float-right d-flex justify-content-between align-content-around settingoption">
								<div class="cross icons bg-success pointer d-flex justify-content-center align-items-center"
									(click)="edit(data,i)">
									<img src="./assets/pencil-edit-button.svg">
								</div>
								<div class="cross icons bg-danger pointer d-flex justify-content-center align-items-center"
									(click)="DeleteFIles(data.FilesId)">
									<img src="./assets/cross.svg">
								</div>
							</div>
						</div>
						<div *ngIf="editContent && contentIndex == i"
							class="w-100 d-flex align-items-center">
							<input type="text" class="form-control w-75" name="textdata" [value]="data.Description"
								[(ngModel)]="textdata" #ctrl="ngModel">
							<div class="d-flex justify-content-between align-content-around settingoption">
								<div class="cross icons bg-success pointer d-flex justify-content-center align-items-center"
									(click)="updateName(data.FilesId)">
									<img src="./assets/checked.svg">
								</div>
								<div class="cross icons bg-dark pointer d-flex justify-content-center align-items-center"
									(click)="esc()">
									<img src="./assets/cross.svg">
								</div>
							</div>
						</div>
					</li>
				</ul>
			</div>
		</div>
	</div>
</div>
	`,
	styles: [`
	.cursor-pointer {
		cursor: pointer;
	  }
	  .node-content-wrapper, .tree-children {
		position: relative;
	  }  
	  .cr-slider-wrap {
		margin: 8px auto !important
	  }
	  .drag {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		opacity: 0;
		z-index:999999;
	  }
	  .dropdownarea {
		width: 100%;
		border: 1px dashed #ccc;
		padding:10px 15px;
		display: flex;
		align-items: center;
		height: 65px;
		text-align: center;
		align-items: center;
		position: relative;
	  }
	  .dropdownarea h1 {
		color: #7d7c7c;
		font-size: 17px;
	  }
	  .pd5 {
		padding: 89px 9px 76px 0px;
		float: left;
		width: 100%;
	  }
	  .table-headed {
		padding: 15px;
		display: block;
	  }
	  .mgt50 {
		margin-top: 50px;
	  }
	  .btndefault {
		background-color: #f3f3f3;
		border-color: #e6e6e6;
		color: #333;
	  }
	  .cross {
		display: inline-block;
		font-size: 12px;
		padding: 5px;
		color: red;
		font-weight: bold;
		width: 10px;
		height: 10px;
		margin-left: 10px;
	  }
	  .progressbar {
		height: 2px;
		background: green;
		float: left;
	  }
	  .fz14 {
		font-size: 14px;
	  }
	  .top50 {
		top: 31px;
		left: -19px;
	  }
	  .successmsg {
		font-size: 14px;
		padding: 10px;
		color: green;
		width: 100%;
		text-align: center;
		position: absolute;
		top: -82px;
	  }
	  .fileuploadlist {
		margin: 0;
		padding: 0;
	  }
	  .fileuploadlist li {
		margin: 0;
		padding: 0;
		width: 100%;
		list-style: none;
		margin-bottom: 10px;
	  }
	  .fileuploadlist li a {
		margin: 0;
		padding: 0;
		font-size: 14px;
		color: #333;
		text-transform: capitalize;
	  }
	  .pointer {
		cursor: pointer;
	  }
	  .cross img {
		height: 10px;
	  }
	  .listdata {
		width: 100%;
		max-width: calc(100% - 100px);
		position: absolute;
		bottom:12px;
		left:77px;
		background-color: #fff;;
		z-index: 9999999;
	  }
	  .mflex{
		display: flex;
		background: #fff;
		align-items: center;
		max-width: calc(100% - 100px);
	  }
	  .top5{
		top:12px;
		position: relative;
	  }
	  .getData{
		top:5px;
	  }
	  .settingoption {
		width: 50px;
	  }
	  .icons {
		width: 20px;
		height: 20px;
		border-radius: 3px;
	  }
	  .checkbox {
		display: flex;
		align-items: center;
		margin: 10px 0;
	  }
	  .styled-checkbox {
		position: absolute;
		opacity: 0;
	  }
	  .styled-checkbox+span {
		position: relative;
		cursor: pointer;
		padding: 0;
	  }
	  .styled-checkbox+span:before {
		content: "";
		margin-right: 10px;
		display: inline-block;
		vertical-align: text-top;
		width: 15px;
		height: 15px;
		border: 1px solid #ccc;
	  }
	  .styled-checkbox:checked+span:after {
		content: "";
		position: absolute;
		left: 5px;
		top: 2px;
		height: 9px;
		width: 5px;
		border: solid #09b4bd;
		border-width: 0 2px 2px 0;
		-webkit-transform: rotate(45deg);
		transform: rotate(45deg);
	  }
	  .procesbar {
		background: #ccc;
		height: 2px;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
	  }
	  .progressContainer {
		width: 100%;
		position: relative;
		top:16px;
	  }
	  .z9 {
		z-index: 99;
		position: relative;
	  }
	  .w79 {
		width: 82%;
	  }
	  .uploadicon {
		padding: 10px;
	  }
	  .w-100 {
		width: 100%;
	  }
	  .mb-5 {
		margin-bottom: 3rem!important
	  }
	  .form-row {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-wrap: wrap;
		flex-wrap: wrap;
		margin-right: -5px;
		margin-left: -5px;
	  }
	  .col-12 {
		flex: 0 0 100%;
		max-width: 100%;
	  }
	  .mb-0 {
		margin-bottom: 0px!important;
	  }
	  .form-row>.col, .form-row>[class*=col-] {
		padding-right: 5px;
		padding-left: 5px;
	  }
	  .d-flex {
		display: flex;
	  }
	  .position-relative {
		position: relative;
	  }
	  .border {
		border: 1px solid #dee2e6!important
	  }
	  .flex-column {
		flex-direction: column
	  }
	  .align-content-around {
		align-content: space-around
	  }
	  .bg-success {
		background-color: #28a745!important;
	  }
	  .bg-danger {
		background-color: #dc3545!important;
	  }
	  .justify-content-center {
		-webkit-box-pack: center!important;
		-ms-flex-pack: center!important;
		justify-content: center!important;
	  }
	  .float-right {
		float: right!important;
	  }
	  .justify-content-between {
		-webkit-box-pack: justify!important;
		-ms-flex-pack: justify!important;
		justify-content: space-between!important;
	  }
	  .floating-border{
		border:1px solid #ccc;
		padding: 15px !important;
		position: relative;
	  }
	`]
})

export class SinglefileuploadComponent implements OnInit {
	
	@Input() idInputFile : string = 'other';
	@Input() label;
	@Input() global_url: any = '';
	@Input() url: any = 'http://apinext.aaadev.info/apinext/';
	@Input() fileurl: any = 'http://apinext.aaadev.info/';
	@Input() FilesGroupIdvalue: any = '0';
	@Input() FileId: any = '0';
	@Input() nameFile: any = '';
	@Input() dataBase: any = 'APPS';
	@Output() fileResponse = new EventEmitter;
	imageSrc: string;
	GenericToken: any;
	// url: string = 'http://apinext.aaadev.info/apinext/uploader/uploadnew';
	// fileurl: string = 'http://apinext.aaadev.info/';
	filename: string;
	height: any;
	width: any;
	filelink: string;
	UploadedFileli: boolean;
	FilesGroupId: string;
	editContent = false;
	isFiles:any = false;
	contentIndex: any;
	datas: any = [];
	description = ''
	textdata: string = '';
	percentDone: number;
	uploadSuccess: boolean;
	httpOptionJSON = {
		headers: new HttpHeaders({
			'Content-Type': 'application/json'
		})
	};
	constructor(private http: HttpClient) {
	}
	ngOnInit() {
		this.getListdata()
	}
	upload(files: File[]) {
		this.uploadAndProgress(files);
	}
	getListdata() {
		this.http.get(this.url + "GetSystemInfo?passcode=flipper613").subscribe(res => {
			this.GenericToken = res["GenericToken"];
			if (this.FilesGroupIdvalue) {
				const body = {
					FilesGroupId: this.FilesGroupIdvalue,
				};
				const url = this.url + "Data/Exec/" + this.dataBase + "/core/FILES_List?api_key=" + this.GenericToken;
				this.http.post(url, body, this.httpOptionJSON).subscribe(res => {
					this.datas = res;
					for (let i = 0; i < this.datas.length; i++) {
						let file_url = this.global_url + this.datas[i].FileUrl;
						this.datas[i].FileUrl = file_url;
						this.isFiles = true;
					}
					this.UploadedFileli = true;
				});
			}
		});
	}
	uploadAndProgress(files: File[]) {
		this.UploadedFileli = false;
		var formData = new FormData();
		formData.append('file', files[0], files[0].name);
		formData.append('id', this.FileId);
		formData.append('filegroupid', this.FilesGroupIdvalue);
		formData.append('mediaType', 'example');
		// formData.append('description', files[0].name);
		if(this.nameFile != ''){
			formData.append('description', this.nameFile);
		}else{
			formData.append('description', files[0].name);
		}

		this.http.post(this.url + 'uploader/uploadnew', formData, { reportProgress: true, observe: 'events' }).subscribe(event => {
			if (event.type === HttpEventType.UploadProgress) {
				this.percentDone = Math.round(100 * event.loaded / event.total);
			} else if (event instanceof HttpResponse) {
				this.uploadSuccess = true;
				this.UploadedFileli = true;
				this.filename = event.body["Description"]
				this.filelink = this.fileurl + event.body["FileUrl"]
				this.isFiles = true
				this.fileResponse.emit(event.body);
				this.getListdata()
				setTimeout(() => {
					this.uploadSuccess = false
					this.percentDone = 0
				}, 3000)
			}
		});
	}
	edit(data, i) {
		this.textdata = data.Description
		this.contentIndex = i;
		this.editContent = true
	}
	updateName(id) {
		var url = this.url + "Models/Files?api_key=" + this.GenericToken
		var body = {
			FilesId: id,
			Description: this.textdata
		}
		this.http.post(url, body).subscribe(res => {
			this.getListdata()
			this.esc()
		})
	}
	esc() {
		this.editContent = false
		this.contentIndex = null
	}
	
	DeleteFIles(value: any): void {
		this.http.delete(this.url + "Models/files/" + value + "?api_key=" + this.GenericToken).subscribe(res => {
			const body = {
				FilesGroupId: this.FilesGroupIdvalue
			};
			const url = this.url + "Data/Exec/" + this.dataBase + "/core/FILES_List?api_key=" + this.GenericToken;
			this.http.post(url, body, this.httpOptionJSON).subscribe(res => {
				this.datas = [];
				this.datas = res;
				this.isFiles = false;
				for (let i = 0; i < this.datas.length; i++) {
					let file_url = this.global_url + this.datas[i].FileUrl;
					this.datas[i].FileUrl = file_url;
				}
				this.UploadedFileli = true;
			});
		})
	}	
}
