import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'sml-radio-check-box-list',
  template: `
  <form class="formRadioCheck">
    <div *ngIf="type == 'checkbox'">
      <div class="inputGroupRadioCheck" *ngFor="let chk of jsonData;let i=index;">
        <input id="{{id}}-{{i}}" name="{{id}}-{{i}}" [value]="chk.isChecked" [(ngModel)]="chk.isChecked" (change)="changeEvent('checkbox')" type="checkbox" />
        <label for="{{id}}-{{i}}">{{chk.name}}</label>
      </div>
    </div>
    <div *ngIf="type == 'radio'">
      <div class="inputGroupRadioCheck" *ngFor="let rad of jsonData;let i=index;">
        <input id="{{id}}-{{i}}" name="{{name}}" [value]="rad.value" [(ngModel)]="selectedValue" (change)="changeEvent('radio')" type="radio" />
        <label for="{{id}}-{{i}}">{{rad.name}}</label>
      </div>
    </div>
  </form>
  `,
  styles: [`
  .inputGroupRadioCheck {
    background-color: #fff;
    display: block;
    margin: 10px 0;
    position: relative;
  }
  .inputGroupRadioCheck  input:checked ~ label {
      color: #fff;
  }
  .inputGroupRadioCheck  input:checked ~ label:before {
      transform: translate(-50%, -50%) scale3d(56, 56, 1);
      opacity: 1;
  }
  .inputGroupRadioCheck  input:checked ~ label:after {
      background-color: #54E0C7;
      border-color: #54E0C7;
  }
  .inputGroupRadioCheck  input {
      width: 32px;
      height: 32px;
      order: 1;
      z-index: 2;
      position: absolute;
      right: 30px;
      top: 50%;
      transform: translateY(-50%);
      cursor: pointer;
      visibility: hidden;
  }
  .inputGroupRadioCheck label {
      padding: 12px 30px;
      width: 100%;
      display: block;
      text-align: left;
      color: #3C454C;
      cursor: pointer;
      position: relative;
      z-index: 2;
      transition: color 200ms ease-in;
      overflow: hidden;
  }
  .inputGroupRadioCheck label:before {
      width: 10px;
      height: 10px;
      border-radius: 50%;
      content: '';
      background-color: #5562eb;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%) scale3d(1, 1, 1);
      transition: all 300ms cubic-bezier(0.4, 0.0, 0.2, 1);
      opacity: 0;
      z-index: -1;
  }
  .inputGroupRadioCheck label:after {
      width: 32px;
      height: 32px;
      content: '';
      border: 2px solid #D1D7DC;
      background-color: #fff;
      background-image: url("data:image/svg+xml,%3Csvg width='32' height='32' viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.414 11L4 12.414l5.414 5.414L20.828 6.414 19.414 5l-10 10z' fill='%23fff' fill-rule='nonzero'/%3E%3C/svg%3E ");
      background-repeat: no-repeat;
      background-position: 2px 3px;
      border-radius: 50%;
      z-index: 2;
      position: absolute;
      right: 30px;
      top: 50%;
      transform: translateY(-50%);
      cursor: pointer;
      transition: all 200ms ease-in;
  }
  .formRadioCheck {
    padding: 0 16px;
    max-width: 550px;
    font-size: 18px;
    font-weight: 600;
  }  
  `]
})
export class RadioCheckBoxListComponent implements OnInit {

  @Input() id: any = 'sml-radio-check-box-list';
  @Input() name: any = 'sml-radio-check-box-list-name';
  @Input() jsonData: any = [];
  @Input() type: any = 'radio';
  @Input() selectedValue: any = '';
  @Output() radioChange = new EventEmitter();
  @Output() checkboxChange = new EventEmitter();
  // ###Samlple Data###
    // this.jsonData = [
    //   {
    //     name:'Option 1',
    //     value:'1',
    //     isChecked:false
    //   },
    //   {
    //     name:'Option 2',
    //     value:'2',
    //     isChecked:true
    //   },
    //   {
    //     name:'Option 3',
    //     value:'3',
    //     isChecked:false
    //   }
  // ]
  constructor() { }
  ngOnInit() {}
  changeEvent(event){
    if(event == 'radio'){
      this.radioChange.emit(this.selectedValue);
    }else{
      this.checkboxChange.emit(this.jsonData);
    }
  }
}
