import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

@Component({
	selector: 'sml-navbar-menu',
	template: `
	<div class="collapse navbar-collapse" id="navbarSupportedContent">
  <ul class="navbar-nav mr-auto">
    <li *ngIf="link" class="nav-item active">
      <a class="nav-link" [ngStyle]="menuStyle" [routerLink]="link">{{label}}</a>
    </li>
    <li class="nav-item dropdown position-relative" *ngIf="link == undefined">
      <a href="javascript:void()" [ngStyle]="menuStyle" class="nav-link dropdown-toggle" data-toggle="dropdown">
        {{label}}
        <b class="caret"></b>
      </a>
      <ul class="dropdown-menu">
        <ng-content></ng-content>
      </ul>
    </li>
  </ul>
</div>
	`,
	styles: [`
	.nav-link {
		color: #ffffff;
	}
	
	.positionRight {
		position: absolute;
		right: 10px;
		list-style: none
	}
	.nav-link{font-size: 13px;}
	
	.dropdown-toggle{font-size: 13px;}
	
	.overlaybtn {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 9;
		display: none;
	}
	
	.dropdown-menu {
		z-index: 9999;
		left: 0;
		left: unset;
	}
	
	.nav-link {
		color: #ffffff;
	}
	
	.userIcon {
		margin-right: 12px
	}
	
	.userIcon img {
		width: 24px
	}
	
	.dropdown-toggle span,.dropdown-menu{font-size: 13px;}
	.dropdown-toggle::after{    top: 3px;
		position: relative;}
	`]
})
export class MenuComponent implements OnInit {

	@Input() label: string;
	@Input() link: string;
	@Input() menuStyle: any;
	
	constructor() { }
	ngOnInit() { }
	
}
