import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
    selector: 'sml-navbar-menu-submenu',
    template: `
  <a *ngIf="link && param" class="dropdown-item" [routerLink]="[link,param]">{{label}}</a>
  <a *ngIf="link && param == undefined" class="dropdown-item" [routerLink]="link">{{label}}</a>
  <a *ngIf="link == undefined" (click)="menuClick()" class="dropdown-item">{{label}}</a>
`,
    styles: [`
  .overlaybtn {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9;
    display: none;
}

.dropdown-menu {
    z-index: 9999;
    right: 0;
    left: unset;
}

.nav-link {
    color: #ffffff;
}

.userIcon {
    margin-right: 12px
}

.userIcon img {
    width: 24px
}

.dropdown-toggle span,.dropdown-menu{font-size: 13px;}
.dropdown-toggle::after{    top: 3px;
    position: relative;}
  `]
})
export class SubmenuComponent implements OnInit {

    @Input() label: string;
    @Input() link: string;
    @Input() param: string;
    @Input() submenuStyle: any;
    @Output() actionClick = new EventEmitter;

    constructor() { }
    ngOnInit() { }
    menuClick() {
        this.actionClick.emit();
    }
}
