import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
	selector: 'sml-navbar-menu-profilemenu-profilesubmenu',
	template: `
	<a *ngIf="link" class="dropdown-item" href="#" [ngStyle]="submenuStyle" [routerLink]="link">{{label}}</a>
	<a *ngIf="!link" class="dropdown-item" style="cursor: pointer;" [ngStyle]="submenuStyle" (click)="clickMenu()">{{label}}</a>
	`,
	styles: [`
	.overlaybtn {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 9;
		display: none;
	}
	.dropdown-menu {
		z-index: 9999;
		right: 0;
		left: unset;
	}
	.userIcon {
		margin-right: 12px
	}
	.userIcon img {
		width: 24px
	}
	.dropdown-toggle span,.dropdown-menu{
		font-size: 13px;
	}
	.dropdown-toggle::after{    
		top: 3px;
		position: relative;
	}
	`]
})

export class ProfileSubMenuComponent implements OnInit {

	@Input() label: string;
	@Input() link: string;
	@Input() submenuStyle: any;
	@Output() menuClick = new EventEmitter;
	constructor() { }

	ngOnInit() { }

	clickMenu(){
		this.menuClick.emit();
	}
}
