import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
	selector: 'sml-progress-bar',
	template: `
	<div class="w-100">
    <div class="progress">
        <div class="progress-bar {{classes}}" role="progressbar" [ngStyle]="{'width': value + '%' }" aria-valuemin="0" aria-valuemax="100">
            {{value}}%
        </div>
    </div>
</div>
	`,
	styles: [`
	.progress-bar-success {
		background-color: #5cb85c;
	}
	.progress-bar-info {
		background-color: #5bc0de;
	}
	.progress-bar-warning {
		background-color: #f0ad4e;
	}
	.progress-bar-danger {
		background-color: #d9534f;
	}
	.progress-bar.active, .progress.active .progress-bar {
		-webkit-animation: progress-bar-stripes 2s linear infinite;
		-o-animation: progress-bar-stripes 2s linear infinite;
		animation: progress-bar-stripes 2s linear infinite;
	}
	`]
})

export class ProgressbarComponent implements OnInit {
	@Input() id: any = 'sml-progress-bar';
  @Input() value: any = 0;
  @Input() type: any = '';
  @Input() color: any = '';
  @Input() classes: any = 'progress-bar-info progress-bar-striped  active';
  // progress-bar-warning progress-bar-striped  active
  // progress-bar-danger
  // progress-bar-info progress-bar-striped
  @Input() interval: any = 100;
  @Output() completed = new EventEmitter();
  identity;
  constructor() {
  }
  ngOnInit() {
    var width = 1;
    this.identity = setInterval(() => {
      this.scene();
    }, this.interval);
  }
  scene() {
    if (this.value >= 100) {
      clearInterval(this.identity);
    } else {
      this.value = this.value + 1;
    }
  }
}
