import { Component, OnInit, ViewChild } from '@angular/core';
import Swal from 'sweetalert2/dist/sweetalert2.js'

@Component({
  selector: 'sml-alert',
  template: `
  <div class="d-flex justify-content-between">
<button (click)="success()" class="btn btn-success btn-sm w-100 mx-1">
  Success
</button>
<button  (click)="error()" class="btn btn-danger btn-sm w-100 mx-1">
  Error
</button>
<button  (click)="info()" class="btn btn-primary btn-sm w-100 mx-1">
Info
</button>
<button  (click)="doubt()" class="btn btn-info btn-sm w-100 mx-1">
  Doubt
</button>
<button  (click)="warning()" class="btn btn-warning btn-sm w-100 mx-1">
  Warning
</button> 
<button class="btn btn-primary btn-sm" (click)="delete()">Delete File</button>
</div>
  `,
  styles: [`
  
  `]
})
export class AlertComponent implements OnInit {

  constructor() { }

  ngOnInit() {

  }

  success(){
    Swal.fire({
      title: 'Done!',
      text: 'This is not implemented yet :/',
      type: 'success'     
    }) 
  }

  error(){    
    Swal.fire({
      title: 'Oops...',
      text: 'Something went wrong!',
      type: 'error',
    })
  }

  info(){
    Swal.fire({
      title: 'Done!',
      text: 'This is not implemented yet :/',
      type: 'info'     
    }) 
  } 

  warning(){
    Swal.fire({
      title: 'Done!',
      imageUrl: 'https://unsplash.it/400/200',
      text: 'This is not implemented yet :/',
      type: 'warning'     
    }) 
  } 

  doubt(){
    Swal.fire({
      title: 'Are you sure?',
      text: 'You will not be able to recover this imaginary file!',
      type: 'question',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      cancelButtonText: 'No, keep it'
    }).then((result) => {
      if (result.value) {
        Swal.fire(
          'Deleted!',
          'Your imaginary file has been deleted.',
          'success'
          )
        // For more information about handling dismissals please visit
        // https://sweetalert2.github.io/#handling-dismissals
      } else if (result.dismiss === Swal.DismissReason.cancel) {
        Swal.fire(
          'Cancelled',
          'Your imaginary file is safe :)',
          'error'
          )
      }
    })
  }

  delete(){
    Swal.fire({
      title: 'Are you sure?',
      text: 'You will not be able to recover this imaginary file!',
      type: 'question',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      cancelButtonText: 'No, keep it'
    }).then((result) => {
      if (result.value) {
        Swal.fire(
          'Deleted!',
          'Your imaginary file has been deleted.',
          'success'
          )
        // For more information about handling dismissals please visit
        // https://sweetalert2.github.io/#handling-dismissals
      } else if (result.dismiss === Swal.DismissReason.cancel) {
        Swal.fire(
          'Cancelled',
          'Your imaginary file is safe :)',
          'error'
          )
      }
    })
  }


}
